const jsSliderReviews = new Swiper('.jsSliderReviews .slider__inner', {
	modules: [Navigation, Pagination],

	pagination: {
		el: ".jsSliderReviews .swiper-pagination",
		clickable: true,
		bulletClass: "slider__pagination-item",
	},

    navigation: {
        nextEl: '.jsSliderReviews .swiper-button-next',
        prevEl: '.jsSliderReviews .swiper-button-prev',
    },

	loop: false,
	slidesPerView: 4,
    spaceBetween: 16,
	speed: 600,

	breakpoints: {
		0:{
			spaceBetween: 16,
			slidesPerView: 1,
		},
		576: {
			spaceBetween: 16,
			slidesPerView: 1,
		},
		768: {
            slidesPerView: 2,
			spaceBetween: 24,
		},

		992: {
            slidesPerView: 3,
			spaceBetween: 16,
		},
        
		1100: {
            slidesPerView: 4,
			spaceBetween: 16,
		},
	}
});

const jsSliderOffers = new Swiper('.jsSliderOffers .slider__inner', {
	modules: [Navigation, Pagination, Autoplay],
	pagination: {
		el: ".jsSliderOffers .swiper-pagination",
        bulletClass: "slider__pagination-item",
		clickable: true,

	},

    navigation: {
        nextEl: '.jsSliderOffers .swiper-button-next',
        prevEl: '.jsSliderOffers .swiper-button-prev',
    },

	loop: true,
	slidesPerView: 1,
	speed: 600,
	centeredSlides: true,
    spaceBetween: 25,   

	breakpoints: {
        0:{
            pagination: {
                el: ".jsSliderOffers .swiper-pagination",
                bulletClass: "slider__pagination-item",
                clickable: true,
        
            },
        },
		992: {
            pagination: {
                bulletClass: "slider__fraction-item",
                renderBullet: function(index, test){
                    let thisIndex = index + 1;
                    return '<span class="slider__fraction-item"> 0' + thisIndex + '</span>'
                },
                bulletActiveClass: "slider__fraction-item--active",
            },
		},
	}
});

const jsSliderIntro = new Swiper('.jsSliderIntro .slider__inner', {
	modules: [Navigation, Pagination, Autoplay],
	pagination: {
		el: ".jsSliderIntro .swiper-pagination",
        bulletClass: "slider__pagination-item",
		clickable: true,
	},

    navigation: {
        nextEl: '.jsSliderIntro .swiper-button-next',
        prevEl: '.jsSliderIntro .swiper-button-prev',
    },

	loop: true,
	slidesPerView: 1,
	speed: 600,
    spaceBetween: 45,   
});

if (window.screen.width < 992) {
    const jsSliderTeachers = new Swiper('.jsSliderTeachers .slider__inner', {
        modules: [Navigation, Pagination, Autoplay],
        pagination: {
            el: ".jsSliderTeachers .swiper-pagination",
            bulletClass: "slider__pagination-item",
            clickable: true,
        },

        loop: false,
        slidesPerView: 2,
        speed: 600,
        spaceBetween: 32,   

        breakpoints: {
            0:{
                spaceBetween: 16,
                slidesPerView: 2,
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 24,   
            },
        }
    });
}

